"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { ChevronLeft, ChevronRight, RotateCcw } from 'lucide-react'
import type { CornellNote } from '@/app/cornell/page'

interface FlashcardsProps {
  note: CornellNote | null
}

export default function Flashcards({ note }: FlashcardsProps) {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [isFlipped, setIsFlipped] = useState(false)

  if (!note || !note.rows.length) {
    return (
      <Card>
        <CardContent className="p-8 text-center">
          <p className="text-gray-600">Nenhum flashcard disponível</p>
        </CardContent>
      </Card>
    )
  }

  // Filter rows that have both keyword and idea
  const validRows = note.rows.filter(row => row.keyword.trim() && row.idea.trim())

  if (validRows.length === 0) {
    return (
      <Card>
        <CardContent className="p-8 text-center">
          <p className="text-gray-600">
            Nenhum flashcard gerado. Adicione palavras-chave e ideias desenvolvidas no editor.
          </p>
        </CardContent>
      </Card>
    )
  }

  const currentRow = validRows[currentIndex]

  const nextCard = () => {
    setCurrentIndex((prev) => (prev + 1) % validRows.length)
    setIsFlipped(false)
  }

  const prevCard = () => {
    setCurrentIndex((prev) => (prev - 1 + validRows.length) % validRows.length)
    setIsFlipped(false)
  }

  const flipCard = () => {
    setIsFlipped(!isFlipped)
  }

  const resetCards = () => {
    setCurrentIndex(0)
    setIsFlipped(false)
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-center">
            Flashcards - {note.subject}
          </CardTitle>
          <div className="text-center text-sm text-gray-600">
            Card {currentIndex + 1} de {validRows.length}
          </div>
        </CardHeader>
        <CardContent>
          {/* Progress Bar */}
          <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
            <div 
              className="bg-blue-600 h-2 rounded-full transition-all duration-300"
              style={{ width: `${((currentIndex + 1) / validRows.length) * 100}%` }}
            />
          </div>

          {/* Flashcard */}
          <div className="relative">
            <Card 
              className={`min-h-[300px] cursor-pointer transition-all duration-500 transform ${
                isFlipped ? 'bg-blue-50 border-blue-200' : 'bg-white border-gray-200'
              } hover:shadow-lg`}
              onClick={flipCard}
            >
              <CardContent className="p-8 flex flex-col justify-center items-center text-center min-h-[250px]">
                {!isFlipped ? (
                  <div className="space-y-4">
                    <div className="text-sm text-gray-500 uppercase tracking-wide">
                      Palavra-chave
                    </div>
                    <div className="text-2xl font-bold text-gray-800">
                      {currentRow.keyword}
                    </div>
                    <div className="text-sm text-gray-400 mt-8">
                      Clique para ver a resposta
                    </div>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <div className="text-sm text-blue-600 uppercase tracking-wide">
                      Ideia Desenvolvida
                    </div>
                    <div className="text-lg text-gray-700 leading-relaxed">
                      {currentRow.idea}
                    </div>
                    <div className="text-sm text-gray-400 mt-8">
                      Clique para voltar à pergunta
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Flip Indicator */}
            <div className="absolute top-4 right-4">
              <Button
                variant="ghost"
                size="sm"
                onClick={(e) => {
                  e.stopPropagation()
                  flipCard()
                }}
                className="opacity-60 hover:opacity-100"
              >
                <RotateCcw className="w-4 h-4" />
              </Button>
            </div>
          </div>

          {/* Navigation Controls */}
          <div className="flex justify-between items-center mt-6">
            <Button
              onClick={prevCard}
              variant="outline"
              disabled={validRows.length <= 1}
              className="flex items-center gap-2"
            >
              <ChevronLeft className="w-4 h-4" />
              Anterior
            </Button>

            <div className="flex gap-2">
              <Button
                onClick={resetCards}
                variant="ghost"
                size="sm"
                className="text-gray-600"
              >
                Reiniciar
              </Button>
              <Button
                onClick={flipCard}
                variant="ghost"
                size="sm"
                className="text-blue-600"
              >
                {isFlipped ? 'Ver Pergunta' : 'Ver Resposta'}
              </Button>
            </div>

            <Button
              onClick={nextCard}
              variant="outline"
              disabled={validRows.length <= 1}
              className="flex items-center gap-2"
            >
              Próximo
              <ChevronRight className="w-4 h-4" />
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Study Progress */}
      <Card>
        <CardContent className="p-4">
          <div className="flex justify-between items-center text-sm text-gray-600">
            <div>
              <span className="font-medium">{validRows.length}</span> flashcards disponíveis
            </div>
            <div>
              Progresso: <span className="font-medium">{currentIndex + 1}/{validRows.length}</span>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Study Tips */}
      <Card className="bg-gray-50">
        <CardContent className="p-4">
          <h4 className="font-medium text-gray-800 mb-2">Dicas de Estudo:</h4>
          <ul className="text-sm text-gray-600 space-y-1">
            <li>• Tente responder mentalmente antes de virar o card</li>
            <li>• Revise os cards que você errou mais vezes</li>
            <li>• Use o método de repetição espaçada para melhor memorização</li>
            <li>• Pratique regularmente para fixar o conteúdo</li>
          </ul>
        </CardContent>
      </Card>
    </div>
  )
}
