import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"
import type { CornellNote } from "@/app/cornell/page"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// Cornell Notes utilities
export const cornellNotesStorage = {
  // Save a note to localStorage
  saveNote: (note: CornellNote): boolean => {
    try {
      localStorage.setItem('currentCornellNote', JSON.stringify(note))
      return true
    } catch (error) {
      console.error('Erro ao salvar nota:', error)
      return false
    }
  },

  // Load current note from localStorage
  loadCurrentNote: (): CornellNote | null => {
    try {
      const savedNote = localStorage.getItem('currentCornellNote')
      if (savedNote) {
        const note = JSON.parse(savedNote)
        return {
          ...note,
          createdAt: new Date(note.createdAt),
          updatedAt: new Date(note.updatedAt)
        }
      }
      return null
    } catch (error) {
      console.error('Erro ao carregar nota:', error)
      return null
    }
  },

  // Clear all notes
  clearAllNotes: (): boolean => {
    try {
      localStorage.removeItem('currentCornellNote')
      return true
    } catch (error) {
      console.error('Erro ao limpar notas:', error)
      return false
    }
  }
}

// Convert Cornell note rows to flashcard format
export const convertToFlashcards = (note: CornellNote) => {
  return note.rows
    .filter(row => row.keyword.trim() && row.idea.trim())
    .map(row => ({
      id: row.id,
      question: row.keyword,
      answer: row.idea,
      subject: note.subject
    }))
}
