"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import CornellNoteEditor from '@/components/CornellNoteEditor'
import Flashcards from '@/components/Flashcards'

export interface CornellNote {
  id: string
  subject: string
  rows: Array<{
    id: string
    keyword: string
    idea: string
  }>
  summary: string
  createdAt: Date
  updatedAt: Date
}

export default function CornellNotesPage() {
  const [currentNote, setCurrentNote] = useState<CornellNote | null>(null)
  const [activeTab, setActiveTab] = useState('editor')

  useEffect(() => {
    // Load any existing note from localStorage
    try {
      const savedNote = localStorage.getItem('currentCornellNote')
      if (savedNote) {
        const note = JSON.parse(savedNote)
        setCurrentNote({
          ...note,
          createdAt: new Date(note.createdAt),
          updatedAt: new Date(note.updatedAt)
        })
      }
    } catch (error) {
      console.error('Erro ao carregar nota salva:', error)
    }
  }, [])

  const handleSaveNote = (note: CornellNote) => {
    try {
      localStorage.setItem('currentCornellNote', JSON.stringify(note))
      setCurrentNote(note)
    } catch (error) {
      console.error('Erro ao salvar nota:', error)
      alert('Erro ao salvar a nota. Tente novamente.')
    }
  }

  const handleNewNote = () => {
    const newNote: CornellNote = {
      id: Date.now().toString(),
      subject: '',
      rows: [{ id: '1', keyword: '', idea: '' }],
      summary: '',
      createdAt: new Date(),
      updatedAt: new Date()
    }
    setCurrentNote(newNote)
    setActiveTab('editor')
  }

  const canShowFlashcards = currentNote && currentNote.rows.some(row => row.keyword.trim() && row.idea.trim())

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <div className="max-w-6xl mx-auto">
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-3xl font-bold text-center text-gray-800">
              Cornell Notes para Medicina
            </CardTitle>
            <p className="text-center text-gray-600 mt-2">
              Sistema de notas para revalidação de diploma médico
            </p>
          </CardHeader>
          <CardContent>
            <div className="flex justify-center gap-4 mb-6">
              <Button onClick={handleNewNote} variant="outline">
                Nova Nota
              </Button>
              {currentNote && (
                <div className="text-sm text-gray-600 flex items-center">
                  Matéria: <span className="font-medium ml-1">{currentNote.subject || 'Não definida'}</span>
                </div>
              )}
            </div>

            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="editor">Editor de Notas</TabsTrigger>
                <TabsTrigger value="flashcards" disabled={!canShowFlashcards}>
                  Flashcards {!canShowFlashcards && '(Adicione conteúdo primeiro)'}
                </TabsTrigger>
              </TabsList>

              <TabsContent value="editor" className="mt-6">
                <CornellNoteEditor 
                  note={currentNote}
                  onSave={handleSaveNote}
                />
              </TabsContent>

              <TabsContent value="flashcards" className="mt-6">
                {canShowFlashcards ? (
                  <Flashcards note={currentNote} />
                ) : (
                  <Card>
                    <CardContent className="p-8 text-center">
                      <p className="text-gray-600">
                        Adicione palavras-chave e ideias no editor para gerar flashcards
                      </p>
                    </CardContent>
                  </Card>
                )}
              </TabsContent>
            </Tabs>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
