"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Trash2, Plus, Printer, Save } from 'lucide-react'
import type { CornellNote } from '@/app/cornell/page'

interface CornellNoteEditorProps {
  note: CornellNote | null
  onSave: (note: CornellNote) => void
}

const MEDICAL_SUBJECTS = [
  'Anatomia',
  'Fisiologia',
  'Patologia',
  'Farmacologia',
  'Clínica Médica',
  'Cirurgia',
  'Pediatria',
  'Ginecologia e Obstetrícia',
  'Medicina Preventiva',
  'Radiologia',
  'Cardiologia',
  'Neurologia',
  'Outro'
]

export default function CornellNoteEditor({ note, onSave }: CornellNoteEditorProps) {
  const [subject, setSubject] = useState('')
  const [customSubject, setCustomSubject] = useState('')
  const [rows, setRows] = useState([{ id: '1', keyword: '', idea: '' }])
  const [summary, setSummary] = useState('')
  const [errors, setErrors] = useState<string[]>([])

  useEffect(() => {
    if (note) {
      setSubject(note.subject)
      setRows(note.rows.length > 0 ? note.rows : [{ id: '1', keyword: '', idea: '' }])
      setSummary(note.summary)
      
      // Check if subject is custom
      if (note.subject && !MEDICAL_SUBJECTS.includes(note.subject)) {
        setSubject('Outro')
        setCustomSubject(note.subject)
      }
    }
  }, [note])

  const addRow = () => {
    const newRow = {
      id: Date.now().toString(),
      keyword: '',
      idea: ''
    }
    setRows([...rows, newRow])
  }

  const removeRow = (id: string) => {
    if (rows.length > 1) {
      setRows(rows.filter(row => row.id !== id))
    }
  }

  const updateRow = (id: string, field: 'keyword' | 'idea', value: string) => {
    setRows(rows.map(row => 
      row.id === id ? { ...row, [field]: value } : row
    ))
  }

  const validateForm = (): boolean => {
    const newErrors: string[] = []
    
    const finalSubject = subject === 'Outro' ? customSubject : subject
    if (!finalSubject.trim()) {
      newErrors.push('Selecione ou digite uma matéria')
    }
    
    const hasValidRows = rows.some(row => row.keyword.trim() && row.idea.trim())
    if (!hasValidRows) {
      newErrors.push('Adicione pelo menos uma palavra-chave com sua ideia desenvolvida')
    }
    
    if (!summary.trim()) {
      newErrors.push('Adicione um resumo')
    }
    
    setErrors(newErrors)
    return newErrors.length === 0
  }

  const handleSave = () => {
    if (!validateForm()) return
    
    const finalSubject = subject === 'Outro' ? customSubject : subject
    const noteToSave: CornellNote = {
      id: note?.id || Date.now().toString(),
      subject: finalSubject,
      rows: rows.filter(row => row.keyword.trim() || row.idea.trim()),
      summary,
      createdAt: note?.createdAt || new Date(),
      updatedAt: new Date()
    }
    
    onSave(noteToSave)
    setErrors([])
    alert('Nota salva com sucesso!')
  }

  const handlePrint = () => {
    if (!validateForm()) return
    
    // Create print content
    const printContent = `
      <html>
        <head>
          <title>Cornell Notes - ${subject === 'Outro' ? customSubject : subject}</title>
          <style>
            @page { 
              size: A4; 
              margin: 2cm; 
            }
            body { 
              font-family: Arial, sans-serif; 
              line-height: 1.4;
              margin: 0;
              padding: 0;
            }
            .header { 
              text-align: center; 
              margin-bottom: 20px; 
              border-bottom: 2px solid #000;
              padding-bottom: 10px;
            }
            .cornell-layout {
              display: grid;
              grid-template-columns: 1fr 2fr;
              gap: 20px;
              min-height: 60vh;
              border: 1px solid #000;
            }
            .keywords-section {
              border-right: 1px solid #000;
              padding: 15px;
            }
            .ideas-section {
              padding: 15px;
            }
            .row {
              margin-bottom: 15px;
              display: flex;
              align-items: flex-start;
            }
            .keyword {
              font-weight: bold;
              margin-bottom: 5px;
            }
            .idea {
              margin-bottom: 10px;
            }
            .summary-section {
              border-top: 1px solid #000;
              padding: 15px;
              margin-top: 20px;
            }
            .summary-title {
              font-weight: bold;
              margin-bottom: 10px;
            }
          </style>
        </head>
        <body>
          <div class="header">
            <h1>Cornell Notes - ${subject === 'Outro' ? customSubject : subject}</h1>
            <p>Data: ${new Date().toLocaleDateString('pt-BR')}</p>
          </div>
          
          <div class="cornell-layout">
            <div class="keywords-section">
              <h3>Palavras-chave</h3>
              ${rows.filter(row => row.keyword.trim()).map(row => 
                `<div class="keyword">${row.keyword}</div>`
              ).join('')}
            </div>
            
            <div class="ideas-section">
              <h3>Ideias Desenvolvidas</h3>
              ${rows.filter(row => row.idea.trim()).map(row => 
                `<div class="idea">${row.idea}</div>`
              ).join('')}
            </div>
          </div>
          
          <div class="summary-section">
            <div class="summary-title">Resumo:</div>
            <div>${summary}</div>
          </div>
        </body>
      </html>
    `
    
    const printWindow = window.open('', '_blank')
    if (printWindow) {
      printWindow.document.write(printContent)
      printWindow.document.close()
      printWindow.print()
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Editor de Notas Cornell</CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {errors.length > 0 && (
          <div className="bg-red-50 border border-red-200 rounded-md p-4">
            <h4 className="text-red-800 font-medium mb-2">Corrija os seguintes erros:</h4>
            <ul className="text-red-700 text-sm space-y-1">
              {errors.map((error, index) => (
                <li key={index}>• {error}</li>
              ))}
            </ul>
          </div>
        )}

        {/* Subject Selection */}
        <div className="space-y-2">
          <Label htmlFor="subject">Matéria</Label>
          <Select value={subject} onValueChange={setSubject}>
            <SelectTrigger>
              <SelectValue placeholder="Selecione uma matéria" />
            </SelectTrigger>
            <SelectContent>
              {MEDICAL_SUBJECTS.map((subj) => (
                <SelectItem key={subj} value={subj}>
                  {subj}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
          
          {subject === 'Outro' && (
            <Input
              placeholder="Digite o nome da matéria"
              value={customSubject}
              onChange={(e) => setCustomSubject(e.target.value)}
            />
          )}
        </div>

        {/* Keywords and Ideas */}
        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <Label className="text-lg font-medium">Palavras-chave e Ideias Desenvolvidas</Label>
            <Button onClick={addRow} variant="outline" size="sm">
              <Plus className="w-4 h-4 mr-2" />
              Adicionar linha
            </Button>
          </div>
          
          <div className="space-y-3">
            {rows.map((row, index) => (
              <div key={row.id} className="grid grid-cols-1 md:grid-cols-2 gap-4 p-4 border rounded-lg">
                <div className="space-y-2">
                  <Label htmlFor={`keyword-${row.id}`}>Palavra-chave {index + 1}</Label>
                  <Input
                    id={`keyword-${row.id}`}
                    placeholder="Digite a palavra-chave"
                    value={row.keyword}
                    onChange={(e) => updateRow(row.id, 'keyword', e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <div className="flex justify-between items-center">
                    <Label htmlFor={`idea-${row.id}`}>Ideia Desenvolvida {index + 1}</Label>
                    {rows.length > 1 && (
                      <Button
                        onClick={() => removeRow(row.id)}
                        variant="ghost"
                        size="sm"
                        className="text-red-600 hover:text-red-800"
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    )}
                  </div>
                  <Textarea
                    id={`idea-${row.id}`}
                    placeholder="Desenvolva a ideia relacionada à palavra-chave"
                    value={row.idea}
                    onChange={(e) => updateRow(row.id, 'idea', e.target.value)}
                    rows={3}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Summary */}
        <div className="space-y-2">
          <Label htmlFor="summary">Resumo</Label>
          <Textarea
            id="summary"
            placeholder="Escreva um resumo geral do tema estudado"
            value={summary}
            onChange={(e) => setSummary(e.target.value)}
            rows={4}
          />
        </div>

        {/* Action Buttons */}
        <div className="flex gap-4 pt-4">
          <Button onClick={handleSave} className="flex-1">
            <Save className="w-4 h-4 mr-2" />
            Salvar Nota
          </Button>
          <Button onClick={handlePrint} variant="outline" className="flex-1">
            <Printer className="w-4 h-4 mr-2" />
            Imprimir
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}
